dphelper.date = {

  toIso: (value, int = 'en') => {
    if (!value) value = ''
    const date = new Date(value)
    if (String(date) == 'Invalid Date') return null //date = new Date()
    const dateTimeFormat = new Intl.DateTimeFormat(int, {
      year: 'numeric', month: 'long', day: 'numeric',
    })
    return dateTimeFormat.format(date)
  },

  toMMDDYYYY: (value) => {
    if (!value) value = ''
    const date = new Date(value)
    return ('0' + (date.getMonth() + 1)).slice(-2) + "" + ('0' + date.getDate()).slice(-2) + "" + date.getFullYear()
  },

  convert: (value, format) => {
    if (!value) return null
    if (!format) format = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"]
    const month = value.substring(0, 2).replace('0', '')
    const day = value.substring(2, 4)
    const year = value.substring(value.length - 4)
    return day + ' ' + format[Number(month) - 1] + ' ' + year
  },

  iso2Epoch: (value) => {
    if (!value) value = ''
    const date = new Date(value)
    const milliseconds = date.getTime()
    return milliseconds
  },

  localIsoTime: (value) => {
    if (!value) value = ''
    const tzoffset = (new Date(value)).getTimezoneOffset() * 60000
    const localISOTime = (new Date(Date.now() - tzoffset)).toISOString().slice(0, -1)
    return localISOTime
  },

  utc: () => {
    const date = new Date()
    return date.toUTCString()
  },

  parse: (value, separator = '/') => {
    if (!value) return null
    const month = value.substring(0, 2)
    const day = value.substring(2, 4)
    const year = value.substring(4, 8)
    return month + separator + day + separator + year
  },

  year: () => {
    return /\d{4}/.exec(Date())[0]
  },

  addDays: (date, days) => {
    const copy = new Date(Number(date))
    copy.setDate(date.getDate() + days)
    return copy
  },

  dateTimeToString: (dateObject) => {
    try {
      return `${dateObject.getFullYear()}${dateObject.getMonth().toString().length > 1
        ? dateObject.getMonth() + 1
        : `0${dateObject.getMonth() + 1}`
        }${dateObject.getDate().toString().length > 1 ? dateObject.getDate() : `0${dateObject.getDate()}`
        }_${dateObject.getHours()}${dateObject.getMinutes()}${dateObject.getSeconds()}`
    } catch (err) {
      // todo: log errors
      return dateObject
    }
  },

  isoToHuman: (value, symbol = "@") => {
    if (!value) return null
    const format = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"]
    const date = new Date(value)
    const year = date.getFullYear()
    let month = (date.getMonth() + 1).toString()
    let dt = date.getDate().toString()
    const t = value.split("T")[1].split(":").slice(0, 2).join(':')


    if (dt.length < 2) dt = '0' + dt
    if (month.length < 2) month = '0' + month

    return (dt + ' ' + format[Number(month) - 1] + ' ' + year + " " + symbol + " " + t)
  },

  fullDate: (custom = null) => {
    // 01-01-1970 00:00:00

    const
      inputDate = new Date(),
      day = Number(inputDate.getDate()),
      month = Number(inputDate.getMonth() + 1),
      year = Number(inputDate.getFullYear()),
      hours = Number(inputDate.getHours()),
      minutes = Number(inputDate.getMinutes()),
      seconds = Number(inputDate.getSeconds()),

      fulldate =
        (day < 10 ? '0' + day : day) + "-" +
        (month < 10 ? '0' + month : month) + "-" +
        year + " " +
        (hours < 10 ? '0' + hours : hours) + ":" +
        (minutes < 10 ? '0' + minutes : minutes) + ":" +
        seconds

    return fulldate
  }

}

