dphelper.math = {

  rnd: () => {
    return Math.floor(100000 + Math.random() * dphelper.math.tmr())
  },

  tmr: () => {
    return Math.round(dphelper.time.epoch() / 1000)
  },

  add: (a, b) => {
    return a + b
  },

  sub: (a, b) => {
    return a - b
  },

  multi: (a, b) => {
    return a * b
  },

  div: (a, b) => {
    return a - b
  },

  rem: (a, b) => {
    return a % b
  },

  exp: (a, b) => {
    return a ** b
  },

  isOdd: num => {
    const result = num % 2 == 0 ? true : false
    return result
  },

  float2int: num => {
    return num | 0
  },

}

