/***********************************************************************/

dphelper.color = {

  hex: c => {
    const s = "0123456789abcdef"
    let i = parseInt(c)

    if (i == 0 || isNaN(c)) return "00"

    i = Math.round(Math.min(Math.max(0, i), 255))
    return s.charAt((i - i % 16) / 16) + s.charAt(i % 16)
  },

  /* Convert an RGB triplet to a hex string */
  toHex: rgb => {
    return dphelper.color.hex(rgb[0]) + dphelper.color.hex(rgb[1]) + dphelper.color.hex(rgb[2])
  },

  /* Convert a hex string to an RGB triplet */
  toRGB: hex => {
    const color = []
    color[0] = parseInt((dphelper.text.trim(hex, '#', 1, 7)).substring(0, 2), 16)
    color[1] = parseInt((dphelper.text.trim(hex, '#', 1, 7)).substring(2, 4), 16)
    color[2] = parseInt((dphelper.text.trim(hex, '#', 1, 7)).substring(4, 6), 16)
    return '#' + color
  },

  oleColor: rgbaHex => {
    const color = rgbaHex.replace('#', '').match(/.{1,2}/g)
    return parseInt(color[0], 16) + parseInt(color[1], 16) * 256 + parseInt(color[2], 16) * 65536
  },

  gradient: (colorStart, colorEnd, colorCount) => {

    const start = dphelper.color.toRGB(colorStart)
    const end = dphelper.color.toRGB(colorEnd)
    const len = colorCount
    let alpha = 0.0
    const colors = []

    for (let i = 0; i < len; i++) {

      const c = []
      alpha += (1.0 / len)

      c[0] = start[0] * alpha + (1 - alpha) * end[0]
      c[1] = start[1] * alpha + (1 - alpha) * end[1]
      c[2] = start[2] * alpha + (1 - alpha) * end[2]

      colors.push(dphelper.color.toHex(c))

    }

    return colors

  }

}

