dphelper._list.scripts.push(description)

/***********************************************************************/

dphelper.load = {

  /*
  Example:
    var cntx = require.context( __dirname + '/scripts/', false , /\.js$/ );
    dphelper.load.import( cntx );
  */
  /*
  import: ( dir , type = false , extension = /\.js$/ ) => {
    let r = require.context( dir , type , extension );
    r.keys().forEach(
      ( key ) => (
        cache[ key ] = r( key )
      )
    );
  },
  */

  all: cntx => {
    cntx.keys()
      .forEach(key => (window['cache'][key] = cntx(key)))
    // example: dphelper.load.all(require['context']('Scripts', true, /\.(js|jsx|ts|tsx)$/))
    return
  },

  file: (element, path) => {
    fetch(path)
      .then(async function (response) {
        const text = await response.text()
        //document.querySelector( element ).innerHTML = dphelper.text.nl2br( String(text) );
        document.querySelector(element).innerHTML = String(text)
      })
      .catch(
        (error) => console.log(error)
      )
    return
  },

  json: file => {
    if (file.match(/.(json)$/i)) return require('' + file + '')
    return
  },

  remote: (path, method = 'GET', headers) => {

    fetch(path, {
      method: method,
      headers: headers
    })
      .then(
        function (response) {
          //return response.json();
          return response
        }
      )
      .then(
        function (myFile) {
          return myFile
        }
      )
      .catch(
        (error) => console.log(error)
      )

    return
  }

}

////////////////////////////////////////////////////////////////////////////////////
dphelper.setProps(window.dphelper, description)



export { }
