const R = 6371e3 // meters

dphelper.coords = {

  degreesToRadians: (degrees) => {
    return degrees * Math.PI / 180
  },

  latToMeters: (points) => {
    if (!Array.isArray(points)) return
    const finalP = R * Math.cos(points[0]) * Math.cos(points[1])
    return finalP / 1000
  },

  toVector: (points) => {
    if (!Array.isArray(points)) return

    const
      x = R * Math.cos(points[1] * Math.PI / 180) * Math.cos(points[0] * Math.PI / 180),
      y = R * Math.cos(points[1] * Math.PI / 180) * Math.sin(points[0] * Math.PI / 180),
      z = R * Math.sin(points[2] * Math.PI / 180)
    return [x, y, z]
  },

  convertToDecDegrees: (deg, minutes, sec, direction) => {
    let dec = deg + minutes / 60 + sec / 3600
    if (direction == 'S' || direction == 'W') dec *= -1
    return dec
  },

  distance: (point1, point2) => {

    if (!Array.isArray(point1) || !Array.isArray(point2)) return

    `Haversine
    formula:	a = sin²(Δφ/2) + cos φ1 ⋅ cos φ2 ⋅ sin²(Δλ/2)
    c = 2 ⋅ atan2( √a, √(1-a) )
    d = R ⋅ c
    where	φ is latitude, λ is longitude, R is earth's radius (mean radius = 6,371km);
    note that angles need to be in radians to pass to trig functions!`

    const
      φ1 = point1[0] * Math.PI / 180, // φ, λ in radians
      φ2 = point2[0] * Math.PI / 180,
      Δφ = (point2[0] - point1[0]) * Math.PI / 180,
      Δλ = (point2[1] - point1[1]) * Math.PI / 180,

      a = Math.sin(Δφ / 2) * Math.sin(Δφ / 2) +
        Math.cos(φ1) * Math.cos(φ2) *
        Math.sin(Δλ / 2) * Math.sin(Δλ / 2),

      c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a)),

      distance = R * c // in metres

    return {
      "km": Number((distance / 1000).toFixed(2)),
      "Mi": Number(((distance / 1000) * 0.621371).toFixed(2)),
      "nMi": Number(((distance / 1000) / 1.852).toFixed(2))
    }
  }

}

