const description = {
  "name": "format",
  "active": true,
  "subCommand": [
    {
      "name": "currency",
      "version": "0.0.1",
      "example": "dphelper.format.currency()",
      "author": "Dario Passariello",
      "creationDate": "20210101",
      "lastMod": "20210101",
      "type": "function",
      "active": true,
      "description": "",
      "subCommand": []
    }, {
      "name": "phoneNumber",
      "version": "0.0.1",
      "example": "dphelper.format.phoneNumber()",
      "author": "Dario Passariello",
      "creationDate": "20210101",
      "lastMod": "20210101",
      "type": "function",
      "active": true,
      "description": "",
      "subCommand": []
    }
  ]
}

dphelper._list.scripts.push(description)

/***********************************************************************/

dphelper.format = {

  currency: (val, int = 'en-US', cur = 'USD') => {
    if (!val || isNaN(val)) val = 0
    const formatter = new Intl.NumberFormat(int, {
      style: 'currency',
      currency: cur,
    })
    // return ( val ).toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,')
    return formatter.format(val)
  },

  ////////////////////////////////////////////////////////////////////////////

  phoneNumber: str => {
    //Filter only numbers from the input
    const cleaned = ('' + str).replace(/\D/g, '')

    //Check if the input is of correct
    const match = cleaned.match(/^(1|)?(\d{3})(\d{3})(\d{4})$/)

    if (match) {
      //Remove the matched extension code
      //Change this to format for any country code.
      const intlCode = (match[1] ? '+1 ' : '')
      return [intlCode, '(', match[2], ') ', match[3], '-', match[4]].join('')
    }

    return null
  }

}

////////////////////////////////////////////////////////////////////////////////////
dphelper.setProps(window.dphelper, description)



export { }
