/***********************************************************************/

const description = {
  "name": "window",
  "active": true,
  "subCommand": [
    {
      "name": "enhancement",
      "version": "0.0.1",
      "example": "dphelper.window.enhancement()",
      "author": "Dario Passariello",
      "creationDate": "20210101",
      "lastMod": "20210101",
      "type": "function",
      "active": true,
      "description": "Use only one time at end of application",
      "subCommand": []
    }, {
      "name": "center",
      "version": "0.0.1",
      "example": "dphelper.window.center({ url, title, name, w, h })",
      "author": "Dario Passariello",
      "creationDate": "20210101",
      "lastMod": "20210101",
      "type": "function",
      "active": true,
      "description": "Use to center popup to screen",
      "subCommand": []
    }, {
      "name": "onBeforeUnLoad",
      "version": "0.0.1",
      "example": "dphelper.window.onBeforeUnLoad()",
      "author": "Dario Passariello",
      "creationDate": "20210101",
      "lastMod": "20210101",
      "type": "function",
      "active": true,
      "description": "message during leave the site",
      "subCommand": []
    }, {
      "name": "purge",
      "version": "0.0.1",
      "example": "dphelper.window.purge()",
      "author": "Dario Passariello",
      "creationDate": "20210101",
      "lastMod": "20210101",
      "type": "function",
      "active": true,
      "description": "Purge from memory",
      "subCommand": []
    }
  ],
}

dphelper._list.scripts.push(description)

/***********************************************************************/

dphelper.window = {

  enhancement: () => {

    window['offScreenBuffering'] = "auto"

    // HIDE STATUS INFORMATION ON BROWSER BAR
    //***************************************

    window.status = ""
    window['defaultStatus'] = ""

    // WINDOW ANIMATION
    //**********************************

    dphelper.window.animationframe()

  },

  animationframe: () => {

    (
      () => {
        return (
          window.requestAnimationFrame ||
          window['webkitRequestAnimationFrame'] ||
          window['mozRequestAnimationFrame'] ||
          window['oRequestAnimationFrame'] ||
          window['msRequestAnimationFrame'] ||
          function (callback) {
            window.setTimeout(callback, 1000 / 120)
          }
        )
      }
    )()
  },

  center: ({ url, title, name, w, h }) => {

    // Fixes dual-screen position                             Most browsers      Firefox
    const
      dualScreenLeft = window.screenLeft !== undefined ? window.screenLeft : window.screenX,
      dualScreenTop = window.screenTop !== undefined ? window.screenTop : window.screenY,

      width = window.innerWidth ? window.innerWidth : document.documentElement.clientWidth ? document.documentElement.clientWidth : screen.width,
      height = window.innerHeight ? window.innerHeight : document.documentElement.clientHeight ? document.documentElement.clientHeight : screen.height,

      systemZoom = width / window.screen.availWidth,
      left = (width - w) / 2 / systemZoom + dualScreenLeft,
      top = (height - h) / 2 / systemZoom + dualScreenTop,

      newWindow = window.open(
        url,
        title,
        `
        scrollbars=yes,
        width=${w / systemZoom},
        height=${h / systemZoom},
        top=${top},
        left=${left}
        `
      )

    if (window.focus) newWindow.focus()

  },

  onBeforeUnLoad: () => {

    let
      is_dirty = false,
      answer = null,
      loadOnce = false

    const
      mex = "You have made some changes which you might want to save.",
      save_button = document.querySelector("#save_button"),

      beforeUnloadListener = event => {
        event.preventDefault()
        return mex
      }

    const nameInput = document.querySelector("#name")

    if (nameInput) {
      nameInput.addEventListener("input", (event) => {
        if (event.target['value'] !== "") {
          addEventListener("beforeunload", beforeUnloadListener, { capture: true })
        } else {
          removeEventListener("beforeunload", beforeUnloadListener, { capture: true })
        }
      })
    }

    /****************************************************************************** */

    const handleEvent = event => {
      if (event.target.type === 'textarea' || event.target.type === 'input')
        if (is_dirty === false) return is_dirty = true
    }

    /****************************************************************************** */

    // CHANGE ON ASIDE CLICK
    if (loadOnce === false) {
      document.body.addEventListener("click", event => {
        if (event.target['tagName'] === 'IMG') {
          if (is_dirty === true) {
            // if the user navigates away from this page via an anchor link,
            //    popup a new boxy confirmation.
            answer = confirm(mex)
            if (answer === true) {
              return is_dirty = false
            } else {
              event.preventDefault()
            }
          }
        }

      })
      loadOnce = true
    }

    /****************************************************************************** */

    addEventListener('popstate', event => {

      if (is_dirty === true) {
        answer = confirm(mex)
        if (answer === true) {
          return is_dirty = false
        } else {
          event.stopImmediatePropagation()
          event.preventDefault()
          return mex
        }
      }

      return

    })

    /****************************************************************************** */

    window.onbeforeunload = event => {
      if ((is_dirty === true) && (answer === null)) {
        event.preventDefault()
        return event.returnValue = mex
        //return mex
      }
      return
    }

  },

  purge: (d = document, time = 10000) => {

    setTimeout(
      () => {
        if (d === undefined) return null

        let a = d['attributes'], i, l, n

        if (a) {
          const l = a.length
          for (let i = 0; i < l; i += 1) {
            const n = a[i].name
            if (typeof d[n] === 'function') { d[n] = null }
            if (typeof d[n] === 'undefined') { d[n] = null }
          }

        }

        a = d['childNodes']

        if (a) {
          const l = a.length
          for (let i = 0; i < l; i += 1) {
            dphelper.purge(d['childNodes'][i])
          }
        }
      }
      , time)

    return

  }

}

////////////////////////////////////////////////////////////////////////////////////
dphelper.setProps(window.dphelper, description)



export { }
