dphelper.consoleDebug = function (parentDom) {

  //parent must be content
  if (!parentDom) throw Error("Parent dom element required!")

  //Private fields
  let _keysMapped = false
  const _oldLog = console.log
  const _oldInfo = console.info
  const _oldWarn = console.warn
  const _oldErr = console.error

  const _appendToDomElement = function (type) {
    let args = Array.prototype.slice.call(arguments[1]).join(" ")
    if (type === TYPE.WARN) args = "<font color=\"#FF6600\">* WARN: " + args
    else if (type === TYPE.ERR) args = "<font color=\"#FF0000\">* ERR: " + args
    else args = "* " + args

    this.domElement.innerHTML += args + "<br/>"
    if (this.autoScroll && this.visible) this.domElement.scrollTop = this.domElement.scrollHeight
  }

  var TYPE = {
    LOG: 0,
    INFO: 1,
    WARN: 2,
    ERR: 3
  }

  this.autoScroll = true

  //Disable all logging features, both this console and globally.
  this.disableConsoleLogging = false

  //Create the element
  const domElement = document.createElement('div')
  domElement.id = 'debugConsole'

  parentDom.appendChild(domElement)
  this.domElement = domElement

  /*******************************************************************************************/

  this.initKeyMappings = () => {

    if (_keysMapped) {
      console.warn('DebugConsole: keys already mapped!')
      return
    }

    state("toggle_console", (function () { this.visible = !this.visible }).bind(this))
    _keysMapped = true
  }

  /*******************************************************************************************/

  // overrideConsole the standard console, by monkey-patching it.
  this.overrideConsole = () => {
    //console.log('123');

    const suppressedWarnings = [
      'Warning: React does not recognize the `%s` prop on a DOM element. If you intentionally want it to appear in the DOM as a custom attribute, spell it as lowercase `%s` instead. If you accidentally passed it from a parent component, remove it from the DOM element.%s',
      'Warning: Using the "className" prop on <View> is deprecated.', `View`
    ]

    // LOG
    const cloneConsoleLog = console.log
    console.log = function filterWarnings(...args) {
      const [msg] = args
      if (!suppressedWarnings.some((entry) => msg.includes(entry))) cloneConsoleLog(...args)
      return
    }

    // INFO
    const cloneConsoleInfo = console.info
    console.info = function filterWarnings(...args) {
      const [msg] = args
      if (!suppressedWarnings.some((entry) => msg.includes(entry))) {
        cloneConsoleInfo(...args)
      }
      return
    }

    // WARN
    const cloneConsoleWarn = console.warn
    console.warn = function filterWarnings(...args) {
      const [msg] = args
      if (!suppressedWarnings.some((entry) => msg.includes(entry))) {
        cloneConsoleWarn(...args)
      }
      return
    }

    // ERROR
    const cloneConsoleError = console.error
    console.error = function filterWarnings(...args) {
      const [msg] = args
      if (!suppressedWarnings.some((entry) => msg.includes(entry))) {
        cloneConsoleError(...args)
      }
      return
    }

    //onError handler
    window.onerror = (errorMsg, url, lineNumber) => {
      const msg = [
        errorMsg,
        " in <a href='",
        url,
        "' target='_blank'>",
        url.split('/').pop(),
        "</a>:",
        lineNumber
      ]

      console.error(msg.join(''))
      return false
    }
  }

  this.overrideConsole()

  /*******************************************************************************************/


  // Clear the log history
  this.clear = function () {
    this.domElement.innerHTML = ''
  }

  // Public properties
  Object.defineProperties(
    this, {
    "visible": {
      "get": function () { return this.domElement.style.display === '' },
      "set": function (value) {
        if (value) {
          this.domElement.style.display = ''
          if (this.autoScroll) this.domElement.scrollTop = this.domElement.scrollHeight
        } else this.domElement.style.display = 'none'
      },
      configurable: true
    },
    //     "top": {
    //       "get": function() { return this.domElement.style.top; },
    //       "set": function( value ) { this.domElement.style.top = value; }
    //     },
    //     "left": {
    //       "get": function() { return this.domElement.style.left; },
    //       "set": function( value ) { this.domElement.style.left = value; }
    //     },
    //     "bottom": {
    //       "get": function() { return this.domElement.style.bottom; },
    //       "set": function( value ) { this.domElement.style.bottom = value; }
    //     },
    //     "right": {
    //       "get": function() { return this.domElement.style.right; },
    //       "set": function( value ) { this.domElement.style.right = value; }
    //     },
    //     "width": {
    //       "get": function() { return this.domElement.style.width; },
    //       "set": function( value ) { this.domElement.style.width = value; }
    //     },
    //     "height": {
    //       "get": function() { return this.domElement.style.height; },
    //       "set": function( value ) { this.domElement.style.height = value; }
    //     }

  }
  )

}

//////////////////////////////////////////////////////////////////////////////////////////////////////
Object.defineProperty(window.dphelper, "consoleDebug", {
  writable: false,
  configurable: false
})

export { }
