///////////////////////////////////////////////////////////////////////////////////

dphelper.iDB = {

  init: () => {

    setTimeout(
      () => {

        const db = "dph-sys"
        const table = "empty"

        if (dphelper.iDB.exist(db) === true) {

          setTimeout(() => {
            dphelper.iDB.toState(db, table, 1)
          }, 2000)

        } else {

          dphelper.iDB.create(db, table)

          setTimeout(() => {
            dphelper.iDB.insert(db, table, 'Ready!')
          }, 2000)

        }
        return
      }
      , 1000
    )

    dphelper.iDB.toState()

  },

  ///////////////////////////////////////////////////////////////////////////////////

  create: (storeName, table) => {

    if (dphelper.iDB.exist(storeName)) {
      top.alert(`Database "${storeName}" already exist`)
      return
    }

    const request = indexedDB.open(storeName, dphelper.iDB.version(storeName))

    request.onerror = (event) => {
      console.debug(event, "Database create error: " + event.target['errorCode'])
      return false
    }

    request.onsuccess = event => {

      const checkDB = event.target['result']
      const v = checkDB.version
      checkDB.close()

      if (dphelper.iDB.version(storeName) === undefined) {
        const request = indexedDB.open(storeName, v + 1)
        request.onupgradeneeded = (event) => {
          const db = event.target['result']
          if (db.objectStoreNames.length != 0) return

          const objectStore = db.createObjectStore(table, { keyPath: 'id', autoIncrement: true })
          objectStore.createIndex('id', 'id', { unique: true })
          db.close()
        }
      }

      dphelper.iDB.refresh()
      return

    }

  },

  ///////////////////////////////////////////////////////////////////////////////////

  open: storeName => {

    if (!dphelper.iDB.exist(storeName)) return

    const indexedDB = window.indexedDB || window['mozIndexedDB'] || window['webkitIndexedDB'] || window['msIndexedDB']

    if (!indexedDB) {
      //console.debug( event );
      console.debug("Your browser doesn't support a skey version of IndexedDB. Such and such feature will not be available.")
      console.debug("Something went badly wrong with your indexedDB!")
    }

    const request = indexedDB.open(storeName, dphelper.iDB.version(storeName))

    request.onsuccess = (event) => {
      // console.debug( 'indexedDB "' + storeName + '" open' );
      // return event.target.result;
      // request.close();
      dphelper.iDB.refresh()
    }

    request.onerror = event => {
      console.debug("Database open error: " + event.target['errorCode'])
      return
    }

    return request

  },

  ///////////////////////////////////////////////////////////////////////////////////
  //INSERT: indexedDB.insert('myDB','Mytable','Mykey','MyValue')

  insert: (storeName, table, value) => {

    if (!dphelper.iDB.exist(storeName)) return

    const request = indexedDB.open(storeName, dphelper.iDB.version(storeName))

    request.onerror = event => console.debug(event, "Database insert error: " + event.target['errorCode'])

    request.onsuccess = (event) => {
      try {
        const db = event.target['result']
        const objectStore = db.transaction([table], "readwrite").objectStore(table)
        objectStore.add({ value })
        db.close()
        dphelper.iDB.refresh()
        //console.debug( "IndexDB inserted" );
      } catch (event) {
        console.debug(event, "IndexDB insert not work")
      }
    }

    return

  },

  ///////////////////////////////////////////////////////////////////////////////////
  //UPDATE: indexedDB.update('myDB','Mykey','MyValue')

  update: (storeName, table, key, value) => {

    if (!dphelper.iDB.exist(storeName)) return

    const request = indexedDB.open(storeName, dphelper.iDB.version(storeName))

    request.onerror = event => console.debug(event, "Database update error: " + event.target['errorCode'])

    request.onsuccess = event => {

      const db = event.target['result']
      const objectStore = db.transaction([table], "readwrite").objectStore(table)

      const req = objectStore.get(+key)

      req.onsuccess = event => {

        const data = event.target.result
        data.value = value
        const objRequest = objectStore.put(data)

        objRequest.onsuccess = event => {
          dphelper.iDB.refresh()
          //console.debug('Success in updating record');
        }

        objRequest.onerror = event => {
          console.debug('Error in updating record')
        }

      }

    }

    return

  },

  ///////////////////////////////////////////////////////////////////////////////////

  add: (db, table) => {

    console.log(dphelper.iDB.version(db))

    const dBOpenRequest = window.indexedDB.open(db, 3)

    dBOpenRequest.onupgradeneeded = (event) => {
      const db = event.target['result']
      // Create an objectStore for this database
      const objectStore = db.createObjectStore(table, {
        keyPath: "taskTitle",
      })

      objectStore.createIndex("hours", "hours", { unique: false })
      objectStore.createIndex("minutes", "minutes", { unique: false })
      objectStore.createIndex("day", "day", { unique: false })
      objectStore.createIndex("month", "month", { unique: false })
      objectStore.createIndex("year", "year", { unique: false })
    }

    // dBOpenRequest.addEventListener("success", (event) => {
    //   const db = event.target['result']
    //   db.addEventListener("versionchange", (event) => {
    //     console.log("The version of this database has changed")
    //   })
    // })

  },

  ///////////////////////////////////////////////////////////////////////////////////

  get: (storeName, table, key) => {

    new Promise(
      (resolve, reject) => {

        const request = indexedDB.open(storeName, dphelper.iDB.version(storeName))

        request.onsuccess = event => {

          const db = event.target['result']

          if (db.objectStoreNames.length === 0) return

          const
            Store = db.transaction([table], "readwrite").objectStore(table),
            req = Store.get(key)

          req.onsuccess = event => {
            const cursor = event.target['result']
            resolve(cursor)
          }

        }

      }
    )

  },

  ///////////////////////////////////////////////////////////////////////////////////

  store: (storeName, table) => {

    if (!dphelper.iDB.exist(storeName)) return

    const request = indexedDB.open(storeName, dphelper.iDB.version(storeName))

    request.onerror = event => {
      console.debug(event, "Database get error: " + event.target['errorCode'])
      return
    }

    request.onsuccess = event => {

      const db = event.target['result']
      if (db.objectStoreNames.length === 0) return
      const Store = db.transaction([table], "readwrite").objectStore(table)
      const curs = Store.openCursor()

      curs.onsuccess = event => {

        const cursor = event.target.result

        if (cursor) {
          const
            key = cursor.primaryKey,
            value = cursor.value

          if (!dphelper.store[storeName]) dphelper.store[storeName] = []
          if (!dphelper.store[storeName][table]) dphelper.store[storeName][table] = []
          if (!dphelper.store[storeName][table][key]) dphelper.store[storeName][table][key] = []
          dphelper.store[storeName][table][key] = value //Store.getAll();
          cursor.continue()
        }
        dphelper.iDB.refresh()
      }

      return

    }

  },

  ///////////////////////////////////////////////////////////////////////////////////

  refresh: () => {
    dphelper.iDB.toState()
    console.debug("iDB refresh")
    return
  },

  ///////////////////////////////////////////////////////////////////////////////////

  toState: async (name, db) => {

    // SAVE SINGLE DB TO STATE
    if (name || db) {

      if (!name || !db) {
        console.debug("promiseToState: Some data are missed!")
        return
      }

      setTimeout(async () => state[name] = await db, 1000)

      return
    }

    // ELSE SAVE ALL DB TO STATE.DATABASES

    const storeDB = []
    let totalDB = 0

    //dphelper.iDB.exist( dbName );

    const promise = indexedDB.databases()

    promise.then(databases => {

      Object.entries(databases).map(storeName => {

        /***********************************************************************/

        const request = indexedDB.open(storeName[1].name, dphelper.iDB.version(storeName[1].version))

        request.onerror = (event) => {
          console.debug(event, "Database get error: " + event.target['errorCode'])
          return
        }

        const item = {
          name: "",
          version: "",
          tables: [],
          size: 0
        }

        let total = 0

        request.onsuccess = async (event) => {

          const db = event.target['result']

          if (db.objectStoreNames.length === 0) return

          if (db && db.name) {

            item['name'] = db.name
            item['version'] = db.version

            // ADD ALL TABLES
            Object.entries(db.objectStoreNames).map(async (dbt, i) => {
              const table = []
              const size = await dphelper.iDB.getTableSize(db.name, dbt[1])

              table['name'] = dbt[1]
              table['size'] = size
              item['tables'].push(Object.assign({}, table))
              total += size
              item['size'] = total
              totalDB += total
              storeDB['total'] = totalDB
              storeDB['update'] = dphelper.time.epoch()
            })

            storeDB.push(item)
          }

          // PUT ON STATE
          dphelper.databases = storeDB

        }

        /***********************************************************************/

      })

      return

    }
    )

  },

  ///////////////////////////////////////////////////////////////////////////////////

  list: () => {
    const promise = indexedDB.databases()
    promise.then(databases => {
      console.table(databases)
      return databases
    })
    return
  },

  ///////////////////////////////////////////////////////////////////////////////////

  version: storeName => {
    let ver
    const promise = indexedDB.databases()
    promise.then(databases => {
      databases.find(v => {
        if (storeName === v.name) return v.version
      })
    })

  },

  ///////////////////////////////////////////////////////////////////////////////////

  size: storeName => {
    //if( !dphelper.iDB.exist( storeName ) ) return;
    const
      databaseNames = [storeName],
      databaseNamesCache = databaseNames.length

    let dbName

    for (let i = 0; i < databaseNamesCache; i++) {
      dbName = databaseNames[i]
      return dphelper.iDB.getDbSize(dbName)
    }

    return
  },

  ///////////////////////////////////////////////////////////////////////////////////

  exist: storeName => {
    if (dphelper.databases?.find(db => db.name === storeName) !== undefined) return true
    return false
  },

  ///////////////////////////////////////////////////////////////////////////////////

  delete: storeName => {

    if (!dphelper.iDB.exist(storeName)) {
      alert(`Database "${storeName}" not exist`)
    } else {
      confirm(
        `Are you sure to remove "${storeName}" database?`,
        () => {
          indexedDB.deleteDatabase(storeName)
          dphelper.iDB.refresh()
        },
        () => { alert("Operation cancelled.") }
      )
    }
    return
  },

  ///////////////////////////////////////////////////////////////////////////////////

  getTableSize: (db, dbName) => {

    //if( !dphelper.iDB.exist( dbName ) ) return;

    return new Promise(
      (resolve, reject) => {

        if (db == null) return reject()
        let size = 0
        db = event.target['result']
        const transaction = db.transaction([dbName])
          .objectStore(dbName)
          .openCursor()

        transaction.onsuccess = function (event) {

          const cursor = event.target.result

          if (cursor) {
            const storedObject = cursor.value
            const json = JSON.stringify(storedObject)
            size += json.length
            cursor.continue()
          } else {
            resolve(size)
          }

        }.bind(this)

        transaction.onerror = err => reject("error in " + dbName + ": " + err)

      })
  },

  ///////////////////////////////////////////////////////////////////////////////////

  getDbSize: (dbName) => {

    //if( !dphelper.iDB.exist( dbName ) ) return;

    return new Promise(
      (resolve, reject) => {

        const request = indexedDB.open(dbName, dphelper.iDB.version(dbName))
        let db
        const dbSize = 0

        request.onerror = event => console.debug("Error on IndexedDB")

        request.onsuccess = event => {

          db = event.target['result']
          const tableNames = [...db.objectStoreNames];

          (
            (tableNames, db) => {

              const tableSizeGetters = tableNames.reduce((acc, tableName) => {
                acc.push(dphelper.iDB.getTableSize(db, tableName))
                return acc
              }, [])

              Promise.all(tableSizeGetters).then(sizes => {

                const data = {}

                function Info(table, size) {
                  this.table = table
                  this.size = size
                }

                tableNames.forEach((tableName, i) => {
                  data[i] = new Info(tableName, dphelper.tool.byteSize(sizes[i]))
                })

                const total = sizes.reduce((acc, val) => { return acc + val }, 0)

                resolve(parseInt(dphelper.tool.byteSize(total)))

              })

            })(tableNames, db)

        }

      })


  },

  ///////////////////////////////////////////////////////////////////////////////////

  printTableInfo: dbName => {

    //if( !dphelper.iDB.exist( dbName ) ) return;

    const
      request = indexedDB.open(dbName, dphelper.iDB.version(dbName)),
      dbSize = 0

    let db

    request.onerror = event => console.debug("Error on IndexedDB")

    request.onsuccess = event => {

      db = event.target['result']
      const tableNames = [...db.objectStoreNames];

      (
        (tableNames, db) => {

          const tableSizeGetters = tableNames.reduce((acc, tableName) => {
            acc.push(dphelper.iDB.getTableSize(db, tableName))
            return acc
          }, [])

          Promise.all(tableSizeGetters).then(sizes => {

            const data = {}
            function Info(table, size) {
              this.table = table
              this.size = size
            }

            tableNames.forEach((tableName, i) => {
              data[i] = new Info(tableName, dphelper.tool.byteSize(sizes[i]))
            })

            console.table(data)

            const total = sizes.reduce((acc, val) => { return acc + val }, 0)

            console.debug("TOTAL: " + dphelper.tool.byteSize(total))

          })

        }
      )(tableNames, db)

    }

  },

  ///////////////////////////////////////////////////////////////////////////////////

  printSizes: () => {

    indexedDB['webkitGetDatabaseNames']().onsuccess = event => {

      const
        databaseNames = event.target.result,
        databaseNamesCache = databaseNames.length

      let dbName

      for (let i = 0; i < databaseNamesCache; i++) {
        dbName = databaseNames.item(i)
        dphelper.iDB.getDbSize(dbName)
      }
    }

    return
  },

  ///////////////////////////////////////////////////////////////////////////////////

  countRows: (dbName, table) => {

    return new Promise(
      (resolve, reject) => {

        const request = indexedDB.open(dbName, dphelper.iDB.version(dbName))

        request.onsuccess = function (event) {

          const db = event.target['result']
          if (db.objectStoreNames.length === 0) return

          const
            transaction = db.transaction([table], 'readonly').objectStore(table),
            countRequest = transaction.count()

          countRequest.onsuccess = function (event) {
            const cursor = event.target['result']
            resolve(cursor)
          }

        }

      })

  }

}

