///////////////////////////////////////////////////////////////////////////////////

dphelper.socket = {

  info: () => {
    return 'Created by Dario Passariello'
  },

  ///////////////////////////////////////////

  start: (element, server) => {
    dphelper.socket.connect(server)
    dphelper.socket.receive(element)
    return
  },

  ///////////////////////////////////////////

  conn: (id, server) => {
    if (!id || !server) {
      console.log('please use connect!')
      return
    }
    const ws = new WebSocket(server)
    ws['binaryType'] = "blob"
    ws['headers'] = {
      test: "test"
    }
    return [ws, id]
  },

  ///////////////////////////////////////////

  connect: (server) => {
    if (!server) return
    dphelper.socket.open(dphelper.security.uuid.v5(), server)
    return
  },

  ///////////////////////////////////////////

  open: (id, server) => {
    if (!id) return
    dphelper.socket.connection = dphelper.socket.conn(id, server)
    //dphelper.log.reg(id)
    dphelper.socket.connection[0].onopen = () => dphelper.log.reg(id + ' connected')
    dphelper.socket.connection[0].onclose = (event) => dphelper.log.debug((event.wasClean) ? 'Disconnected' : 'Connection break: ' + (event.reason || event.code))
    dphelper.socket.connection[0].onerror = (err) => dphelper.log.error(err.message)
    // socket.onmessage = ( event ) => console.debug( event.data ) }
    return
  },

  ///////////////////////////////////////////

  send: (mex, type = "reg") => {
    if (!dphelper.socket.connection[1] || !mex) return
    const ws = dphelper.socket.connection[0]
    const id = dphelper.socket.connection[1]
    //ws.addEventListener( 'open' , function ( event ) { console.log( event ) });
    const obj = `{"${type}":"(${id}:${mex})"}`
    //dphelobjper.log.reg(obj)
    ws.send(obj)
    return
  },

  ///////////////////////////////////////////

  ping: () => {
    if (!dphelper.socket.connection[1]) return
    const ws = dphelper.socket.connection[0]
    const id = dphelper.socket.connection[1]
    //ws.addEventListener( 'open' , function ( event ) { console.log( event ) });
    const obj = `{"debug":"__ping__"}`
    ws.send(obj)
    return
  },

  ///////////////////////////////////////////

  receive: element => {
    //if (!dphelper.socket.connection[1]) return
    dphelper.ws = dphelper.socket.connection[0]

    dphelper.ws.addEventListener('message', function (message) {

      try {

        const data = Object.values(JSON.parse(message.data))
        dphelper.log.reg(message.data)

        if ($(element)) {
          $(element).prepend(data[0] + "<br/>")
          //$( element ).append( data[0] + "<br/>" )
        }

        return data[0]

      } catch (e) {

        // return false
        // console.error(`error: ${e}`)

      }

    })
    return
  },

  keepAlive: () => {

    let tm
    function ping() {
      dphelper.socket.ping()
      tm = setTimeout(function () {
        // console.log('ping')
        /// ---connection closed ///
      }, 5000)
    }

    function pong() {
      // console.log('pong')
      clearTimeout(tm)
    }

    dphelper.ws.onopen = function () {
      // console.log('open')
      setInterval(ping, 30000)
    }

    dphelper.ws.onmessage = function (evt) {

      try {

        const ck = Object.values(JSON.parse(evt.data))
        // console.log( evt )
        // console.log( '>', ck[0] )
        if (ck[0] === '__ping__') {
          pong()
          return
        }

      } catch (e) {

        // return false
        // console.error(`error: ${e}`)

      }

      //////-- other operation --//
    }

    return
  }

}

////////////////////////////////////////////////////////////////////////////////////
dphelper.setProps(window.dphelper, description)



