const description = {
  "name": "translators",
  "active": true,
  "subCommand": [
    {
      "name": "convertMatrixToScale",
      "version": "0.0.1",
      "example": "dphelper.translators.convertMatrixToScale(...)",
      "author": "Dario Passariello",
      "creationDate": "20210101",
      "lastMod": "20210101",
      "type": "function",
      "active": true,
      "description": "",
      "subCommand": []
    }
  ]
}

dphelper._list.scripts.push(description)

/***********************************************************************/

dphelper.translators = {

  convertMatrixToScale: values => {

    values = values.split('(')[1]
    values = values.split(')')[0]
    values = values.split(',')

    const
      a = values[0],
      b = values[1],
      scale = Math.sqrt(a * a + b * b)

    return scale
  }

}

////////////////////////////////////////////////////////////////////////////////////
dphelper.setProps(window.dphelper, description)

export { }
