const description = {
  "name": "screen",
  "active": true,
  "subCommand": [
    {
      "name": "fullScreen",
      "version": "0.0.1",
      "example": "dphelper.screen.fullScreen(...)",
      "author": "Dario Passariello",
      "creationDate": "20210101",
      "lastMod": "20210101",
      "type": "function",
      "active": true,
      "description": "",
      "subCommand": []
    }, {
      "name": "toggle",
      "version": "0.0.1",
      "example": "dphelper.screen.toggle(...)",
      "author": "Dario Passariello",
      "creationDate": "20210101",
      "lastMod": "20210101",
      "type": "function",
      "active": true,
      "description": "",
      "subCommand": []
    }
  ]
}

dphelper._list.scripts.push(description)

/***********************************************************************/

dphelper.screen = {

  fullScreen: el => {

    if (el.requestFullScreen) {
      el.requestFullScreen()
    } else if (el.mozRequestFullScreen) {
      el.mozRequestFullScreen()
    } else if (el.webkitRequestFullScreen) {
      el.webkitRequestFullScreen()
    }

  },

  toggle: el => {

    const requestFullScreen = el.requestFullscreen || el.mozRequestFullScreen || el.webkitRequestFullScreen || el.msRequestFullscreen
    const cancelFullScreen = el.exitFullscreen || el.mozCancelFullScreen || el.webkitExitFullscreen || el.msExitFullscreen

    !el.fullscreenElement && !el.mozFullScreenElement && !el.webkitFullscreenElement && !el.msFullscreenElement
      ? requestFullScreen.call(el)
      : cancelFullScreen.call(el)

  },

  info: () => {
    const final = [
      screen.width,
      screen.height,
      screen.availWidth,
      screen.availHeight,
      screen.colorDepth,
      screen.pixelDepth
    ]
    return final
  }

}

// dphelper.fullScreen.go( document.body )
// dphelper.fullScreen.toggle( document.body )

////////////////////////////////////////////////////////////////////////////////////
dphelper.setProps(window.dphelper, description)



export { }
