dphelper.store = {

  data: [],

  get: name => {
    if (!name) return
    return window.store.data[name]
  },

  set: (name, value, protect = false) => {
    if (!name) return
    if (protect === true) value = [...value, protect = true]
    window.store.data[name] = value
    return
  },

  update: (name, value) => { },

  clear: () => { },

  clearAll: () => { },

  delete: () => { }

}

// SET STORE
window.store = dphelper.store

