const description = {
  "name": "memory",
  "active": true,
  "subCommand": [
    {
      "name": "state",
      "version": "0.0.2",
      "example": "state.key = value",
      "author": "Dario Passariello",
      "creationDate": "20210101",
      "lastMod": "20230111",
      "type": "function",
      "active": true,
      "description": "",
      "subCommand": []
    }, {
      "name": "lock",
      "version": "0.0.1",
      "example": "lockState('state[key]')",
      "author": "Dario Passariello",
      "creationDate": "20230101",
      "lastMod": "20230111",
      "type": "function",
      "active": true,
      "description": "Lock a state to be overrrided",
      "subCommand": []
    }, {
      "name": "unlock",
      "version": "0.0.1",
      "example": "unlockState('state[key]')",
      "author": "Dario Passariello",
      "creationDate": "20230101",
      "lastMod": "20230111",
      "type": "function",
      "active": true,
      "description": "Unlock a state",
      "subCommand": []
    }, {
      "name": "delete",
      "version": "0.0.2",
      "example": "state.delete([key])",
      "author": "Dario Passariello",
      "creationDate": "20210101",
      "lastMod": "20230111",
      "type": "function",
      "active": true,
      "description": "",
      "subCommand": []
    }
  ]
}

dphelper._list.scripts.push(description)

/***********************************************************************/

dphelper.memory = {

  state: (key, value) => {

    // LIST ALL ARRAY FROM STATE
    if (!key && !value) {

      // NEW ARRAY
      const final = []

      // GET CLEAN ARRAY FROM JSON
      const clear = dphelper.obj.toArray(state)

      // FINAL ARRAY (remove functions)
      clear.map(
        (list, i) => {
          if (typeof list[1] === 'function') return
          final[list[0]] = list[1]
          //final.push( list );
        }
      )

      return dphelper.array.toObj(final)

    } else {

      // CHECK
      if ((!key && value)) {
        console.debug('Error: State works in this way: state( key, value ) or only key')
        void 0

        // RETURN
      } else if (key && !value) {
        return state[key]

        // SAVE
      } else if (key && value) {

        // check if is a function
        if (typeof state[key] === 'function') {
          return `Error, ${key} is a protect function name`
        } else {
          // if ok register
          state[key] = value
        }

      }

    }

    return

  },

  lock: obj => {
    const s = obj?.split(".")
    if (s.length < 2) return
    Object.defineProperty(
      window[s[0]],
      s[1],
      {
        writable: false,
        configurable: false
      }
    )
    return
  },

  unlock: obj => {
    const s = obj?.split(".")
    if (s.length < 2) return
    Object.defineProperty(
      window[s[0]],
      s[1],
      {
        writable: true,
        configurable: true
      }
    )
    console.log(Object.getOwnPropertyDescriptor(window[s[0]], s[1]))
    return
  },

  delete: (key) => state[key] = undefined

}

// SIMPLIFY STATE CALL
// window.state.delete = window?.dphelper?.memory?.delete
// state = window?.dphelper?.memory?.state
// window.state = state()

// FIX OLD DPHELPER
window.dphelper.state = () => state

Object.defineProperty(window, "state", {
  writable: false,
  configurable: false
})

////////////////////////////////////////////////////////////////////////////////////
dphelper.setProps(window.dphelper, description)



export { }
