const description = {
  "name": "promise",
  "active": true,
  "subCommand": [
    {
      "name": "check",
      "version": "0.0.1",
      "example": "dphelper.promise.check(...)",
      "author": "Dario Passariello",
      "creationDate": "20210101",
      "lastMod": "20210101",
      "type": "function",
      "active": true,
      "description": "",
      "subCommand": []
    }, {
      "name": "resolve",
      "version": "0.0.1",
      "example": "dphelper.promise.resolve(...)",
      "author": "Dario Passariello",
      "creationDate": "20210101",
      "lastMod": "20210101",
      "type": "function",
      "active": true,
      "description": "",
      "subCommand": []
    }
  ]
}

dphelper._list.scripts.push(description)

/***********************************************************************/

dphelper.promise = {

  check: p => {
    return p && Object.prototype.toString.call(p) === "[object Promise]"
  },

  resolve: async data => {
    const p = {}
    if (dphelper.promise.check(data)) {
      Promise.resolve(data).then(function (value) {
        p['promise'] = value
      })
      return await p
    }
    return
  },

}

////////////////////////////////////////////////////////////////////////////////////
dphelper.setProps(window.dphelper, description)



export { }
