const description = {
  "name": "shortcut",
  "active": true,
  "subCommand": [
    {
      "name": "keys",
      "version": "0.0.1",
      "example": "dphelper.shortcut.keys([e, trigger])",
      "author": "Dario Passariello",
      "creationDate": "20210101",
      "lastMod": "20210101",
      "type": "function",
      "active": true,
      "description": "",
      "subCommand": []
    }
  ]
}

dphelper._list.scripts.push(description)

/***********************************************************************/

dphelper.shortcut = {

  keys: (e, trigger) => {
    const code = e.which ? e.which : e.keyCode;

    (e) = {
      tes: console.log('try')
    }

    if (!trigger) trigger = {

      "data": [
        {
          "key": "83",
          "ctrl": true,
          "active": true,
          "description": "CTRL+S",
          //"element" : "#formSave",
          "function": ""
        },
        {
          "key": "27",
          "ctrl": false,
          "active": true,
          "description": "ESC",
          "element": "",
          "function": ""
        },
        {
          "key": "80",
          "ctrl": true,
          "active": true,
          "description": "CTRL+P",
          "element": "",
          "function": "function(){ Print }"
        }
      ]
    }

    trigger.map(list => {

      // IF CTRL COMMAND
      if (e.ctrlKey || e.metaKey && list.ctrl === true) {
        if (code === parseInt(list.key) && list.active === true) {
          if (list.element) list.element.click()
          if (list.function) JSON.stringify(list.function)
          e.preventDefault()
          return
        }
      }

    })

  }

}

////////////////////////////////////////////////////////////////////////////////////
dphelper.setProps(window.dphelper, description)



export { }
