const CookieType = "Lax"
let CookieSecure = "false"
const CookieSameSite = "false"

if (location.protocol === 'https:') CookieSecure = "Secure"

dphelper.cookie = {

  // CREATE THE COOKIE

  set: (cname, value, time, path = '/') => {
    const d = new Date()
    d.setTime(d.getTime() + 3600 * 1000 * 24 * 365)

    if (!time) time = d['toGMTString']()
    if (cname) {
      document.cookie = cname + '=' + value + ';expires=' + time + ';path=' + path + ';SameSite=' + CookieSameSite + ';requireSSL=' + CookieSecure + ';' + CookieSecure
    }
  },

  // GET THE COOKIE

  get: (cname) => {
    const asCookies = document.cookie.split("; ")
    const asCookiesCache = asCookies.length

    for (let i = 0; i < asCookiesCache; i++) {
      const asCookie = asCookies[i].split("=")
      if (cname === asCookie[0]) return (unescape(asCookie[1]))
    }

    return null
  },

  // DELETE THE COOKIE

  delete: (cname) => {
    document.cookie = cname + '=;expires=Thu, 01 Jan 1970 00:00:01 GMT;path=/;SameSite=' + CookieSameSite + ';requireSSL=' + CookieSecure + ';' + CookieSecure
  },

  // CLEAR ALL COOKIE

  clearAll: () => {

    const
      cookies = document.cookie.split(";"),
      asCookiesCache = cookies.length

    for (let i = 0; i < asCookiesCache; i++) {

      const
        cookie = cookies[i],
        eqPos = cookie.indexOf("="),
        cname = eqPos > -1 ? cookie.slice(0, eqPos) : cookie

      dphelper.cookie.delete(cname)

    }

    return
  }

}

