const description = {
  "name": "anchor",
  "active": true,
  "subCommand": [
    {
      "name": "toOnClick",
      "type": "function",
      "version": "0.0.1",
      "example": "dphelper.anchor.toOnClick([element], 'foo' | '.foo' | '#foo')",
      "description": `This tools allows you to convert all links in an element to 'onclick'.
      It allows you to avoid the message with the path relative to the requested address
      and it is very useful when you work with SPA but you want to keep even older browsers.`,
      "author": "Dario Passariello",
      "creationDate": "20210101",
      "lastMod": "20210101",
      "active": true,
      "subCommand": []
    }
  ],
}

dphelper._list.scripts.push(description)

/***********************************************************************/

dphelper.anchor = {

  toOnClick: (el) => {

    $(el + ' a').each(function (index) {
      const elem = $(this)
      const href = elem.attr('href')

      if (href) {
        elem
          .on('mouseup', function () {
            //Loader('body', null)
          })
          .css('cursor', 'pointer')
          .addClass(dphelper.pathRail()[3].replace(/\//g, ''))
          .removeAttr('href')
          .on('click', function () {
            dphelper.browser.href(href)
            dphelper.browser.push('', '', href)
          })
      }
    })

    return

  }

}

////////////////////////////////////////////////////////////////////////////////////
dphelper.setProps(window.dphelper, description)



export { }
