/***********************************************************************/

dphelper.type = {

  of: p => { return typeof p },

  instOfObj: p => { return p instanceof Object },

  isNaN: p => { return function (i) { return !p(i) } },

  isPrime: (number) => {

    let divisor = number / 2
    let prime = true

    while (divisor > 1) {
      if (number % divisor === 0) {
        prime = false
        divisor = 0
      } else {
        divisor -= 1
      }
    }
    return prime === true
  },

  isBool: val => {
    if (!val || val.length == null) return true
    return val === '1' ? true : false
  }

}

