/***********************************************************************/

const description = {
  "name": "event",
  "active": true,
  "subCommand": [
    {
      "name": "multi",
      "version": "0.0.1",
      "example": "dphelper.event.multi([element, eventNames, listener])",
      "author": "Dario Passariello",
      "creationDate": "20210101",
      "lastMod": "20210101",
      "type": "function",
      "active": true,
      "description": "",
      "subCommand": []
    }, {
      "name": "dispatch",
      "version": "0.0.1",
      "example": "dphelper.event.dispatch([name, value])",
      "author": "Dario Passariello",
      "creationDate": "20210101",
      "lastMod": "20210101",
      "type": "function",
      "active": true,
      "description": "",
      "subCommand": []
    }, {
      "name": "copy",
      "version": "0.0.1",
      "example": "dphelper.event.copy([element])",
      "author": "Dario Passariello",
      "creationDate": "20210101",
      "lastMod": "20210101",
      "type": "function",
      "active": true,
      "description": "",
      "subCommand": []
    }
  ]
}

dphelper._list.scripts.push(description)

/***********************************************************************/

dphelper.event = {

  multi: (element, eventNames, listener) => {
    const events = eventNames.split(' ')
    const eventsCache = events.length
    for (let i = 0, iLen = eventsCache; i < iLen; ++i) {
      element.addEventListener(events[i], listener, false)
    }

    return
  },

  dispatch: (name, value) => {
    document.dispatchEvent(new CustomEvent(name, value))
  },

  copy: (el) => {
    var e = e || event

    const ele = document.querySelector(el)
    ele.addEventListener("click", async (e) => {
      if (!navigator.clipboard) return
      try {
        console.log(e, e.srcElement.textContent)
        await navigator.clipboard.writeText(e.srcElement.textContent)
        alert("copied")
      } catch (err) {
        console.error("Can't copy the value", err)
      }
    })

    return
  }

}

////////////////////////////////////////////////////////////////////////////////////
dphelper.setProps(window.dphelper, description)

Object.defineProperty(window.dphelper.event, 'dispatch', {
  // isTrusted: true,
  writable: false,
  configurable: false
})



export { }
