/***********************************************************************/

const description = {
  "name": "trigger",
  "active": true,
  "subCommand": [
    {
      "name": "click",
      "version": "0.0.1",
      "example": "dphelper.trigger.click(...)",
      "author": "Dario Passariello",
      "creationDate": "20210101",
      "lastMod": "20210101",
      "type": "function",
      "active": true,
      "description": "",
      "subCommand": []
    }, {
      "name": "change",
      "version": "0.0.1",
      "example": "dphelper.trigger.change(...)",
      "author": "Dario Passariello",
      "creationDate": "20210101",
      "lastMod": "20210101",
      "type": "function",
      "active": true,
      "description": "",
      "subCommand": []
    }
  ]
}

dphelper._list.scripts.push(description)

/***********************************************************************/

dphelper.trigger = {

  click: elem => {
    const selector = document.querySelector(elem)
    if (selector) selector.click()
    return
  },

  change: elem => {
    const selector = document.querySelector(elem)
    const ev = new Event('input', { bubbles: true })
    ev['simulated'] = true
    //selector.value = 'Something new';
    selector.dispatchEvent(ev)
    return
  }

}

////////////////////////////////////////////////////////////////////////////////////
dphelper.setProps(window.dphelper, description)



export { }
