dphelper.console = {

  info: (name, message, func) => {
    console.groupCollapsed('%c' + name + ':%c', "color:orange", "")
    console.debug(message, dphelper[func])
    console.groupEnd()
  },

  stop: (options = [
    'assert',
    'clear',
    'count',
    'debug',
    'dir',
    'dirxml',
    'error',
    'exception',
    'group',
    'groupCollapsed',
    'groupEnd',
    'info',
    'log',
    'markTimeline',
    'profile',
    'profileEnd',
    'table',
    'time',
    'timeEnd',
    'timeline',
    'timelineEnd',
    'timeStamp',
    'trace',
    'warn',
    'verbose'
  ]) => {

    console.clear()
    console['disableYellowBox'] = true
    //console = {}
    console.log = () => { }
    window.console = console
    //if (!window.console) window.console = {}
    const noop = () => { }

    options.forEach((method) => {
      window.console[method] = noop
    })

  }

}

////////////////////////////////////////////////////////////////////////////////////
dphelper.setProps(window.dphelper, description)



export { }
