dphelper.disable = {

  select: function (el = 'body') {

    const disabling = (el, e) => {

      const target = $(e.target)

      if (target.is("input, button, select, textarea, .selectable *")) {
        //console.log( 'ok' )
      } else {
        $('*').trigger('blur')
        e.preventDefault()
        return false
      }

      $('img, a, input, button')
        .on('dragstart', function (e) {
          e.preventDefault()
          return false
        })

    }

    $(el).on("mousedown", function (e) { disabling(el, e) })

    console.debug('%cSelection Disabled: %c' + true, "color:orange", "")

    return

  },

  spellCheck: (tmr = 5000) => {

    setInterval(() => {
      const inputs = document.querySelectorAll("input[type=text], textarea, code")
      const inputsCache = inputs.length

      for (let i = 0; i < inputsCache; i++) {
        if (!inputs[i].getAttribute("spellcheck")) {
          inputs[i].setAttribute("spellcheck", "false")
        }
      }
    }, tmr)

    return

  },

  rightClick: (el = 'body') => {

    $(document).ready(function () {
      $(el).on("contextmenu", function (e) {
        return false
      })
    })

    return

  }

}

////////////////////////////////////////////////////////////////////////////////////
dphelper.setProps(window.dphelper, description)



export { }
