const description = {
  "name": "font",
  "active": true,
  "subCommand": [
    {
      "name": "fitContainer",
      "version": "0.0.1",
      "example": "dphelper.font.fitContainer([element])",
      "author": "Dario Passariello",
      "creationDate": "20210101",
      "lastMod": "20210101",
      "type": "function",
      "active": true,
      "description": "",
      "subCommand": []
    }
  ]
}

dphelper._list.scripts.push(description)

/***********************************************************************/

dphelper.font = {

  fitContainer: (el) => {
    if (!el) return

    function fitStart(el) {
      const divs = document.querySelectorAll(el)
      const divsCache = divs.length

      if (divsCache <= 0) return

      for (let i = 0; i < divsCache; i++) {
        const fontSize = divs[i].offsetWidth * 0.05
        divs[i].style.fontSize = fontSize + 'px'
      }
    }

    dphelper.addListenerMulti(window, 'load resize', () => {
      fitStart(el)
    })

    return

  }

}

////////////////////////////////////////////////////////////////////////////////////
dphelper.setProps(window.dphelper, description)



export { }
